.modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.modal {
  z-index: 101;
  border: 1px solid black;
  background-color: white;
  min-width: 50%;
  -webkit-box-shadow: 6px 6px 4px #111;
          box-shadow: 6px 6px 4px #111;
}

.modal-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0.5rem;
  margin: 0;
  background-color: #512888;
  color: white;
  font-size: 1.6rem;
}

.modal-body {
  padding: 0.5rem;
}



